import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import TimeManager from "../components/TimeManager"


export default ({ data }) => {
    const title = data.transcriptionsJson.title;
    const youtube = data.transcriptionsJson.youtube;
    const body = data.transcriptionsJson.body;

    return (
        <Layout secondtitle={title}>
            <TimeManager youtube={youtube} body={body} />
        </Layout>
    )
}

export const pageQuery = graphql`
  query TranscriptionQuery($id: String) {
    transcriptionsJson(id: { eq: $id }) {
      id
      title
      youtube
      date
      fields {
        slug
      }
      body {
        person
        heading
        text
        paragraphs {
          lines {
            start
            end
            text
          }
        }
      }
    }
  }
`