import React, { useState, useEffect } from "react"

import subtitleCSS from "./subtitles.module.css"

const SCROLL_POSITION = 200

export default function Subtitle({children, start, reach, current, onClick, autoScroll}){
  const [className, setClassName] = useState(subtitleCSS.base);

  const myRef = React.createRef();

  useEffect(() => {
    setClassName(`
      ${subtitleCSS.base}
      ${reach ? subtitleCSS.reach : ''}
      ${current ? subtitleCSS.current : ''}
    `)
  }, [reach, current]);

  useEffect(() => {
    if(current && autoScroll) {
      const { top } = myRef.current.getBoundingClientRect()
      const value = top + window.scrollY + document.documentElement.clientTop
      
      window.scroll({top: value - SCROLL_POSITION, behavior: 'smooth'})
    }
  }, [current, autoScroll]);

  return (
    <span ref={myRef} className={className} onClick={() => onClick(start)}>
      {children}
    </span>
  );
}