import React, { useState, useEffect } from "react"
import { useStaticQuery, graphql } from "gatsby"

import Paper from '@material-ui/core/Paper';
import Avatar from "./Avatar"
import Paragraph from "./Paragraph"

import personCSS from "./Person.module.css"

export const QUERY = graphql`
  fragment PersonsInformation on Query {
    personsJson {
      persons {
        id
        name
        picture {
          childImageSharp {
            fixed(width: 60, height: 60) {
              ...GatsbyImageSharpFixed
            }
          }
        }
      }
    }
  }
`

export default function Person({data, ...otherProps}) {
  const { personsJson : { persons } } = useStaticQuery(
    graphql`query {...PersonsInformation}`
  )

  const { person, paragraphs, reach, current } = data
  const [className, setClassName] = useState(personCSS.base);

  useEffect(() => {
    setClassName(`
      ${personCSS.base}
      ${reach ? personCSS.reach : ''}
      ${current ? personCSS.current : ''}
    `)
  }, [reach, current]);


  return(
    <div className={personCSS.row}>
      <Avatar {...persons[person]}></Avatar>

      <Paper className={className}>
        {paragraphs.map( (paragraph, index) => 
          <Paragraph key={index} lines={paragraph.lines} reach={paragraph.reach} current={paragraph.current} {...otherProps} />
        )}
      </Paper>
    </div>
  )
}