import React, { useState, useEffect } from "react"
import YouTube from 'react-youtube';
import useInterval from 'react-useinterval';
import Paper from '@material-ui/core/Paper';
import useScrollTrigger from '@material-ui/core/useScrollTrigger';

import videoCSS from "./Video.module.css"

//const TICKET_DELAY = 250
//const GETTIME_COMMAND = window.JSON.stringify( { event: 'command', func: 'getCurrentTime' } );

export default function Video({visible, height, width, videoId, onTick, seekTo, hoverOn}) {
  const [player, setPlayer] = useState(null)
  const [playing, setPlaying] = useState(false)
  const [eventListener, setEventListener] = useState(false)
  const [currentTime, setCurrentTime] = useState(0)
  const [boxClassName, setBoxClassName] = useState(videoCSS.base);

  const scrollTrigger = useScrollTrigger();

  function onYoutubeReady(event) {
    setPlayer(event.target);
  }

  function onPlay(event){
    setPlaying(true)
  }

  function onStop(event){
    setPlaying(false)
  }

  useEffect(() => {
    if(eventListener) {
      console.log("AAAAA: ", eventListener)
      window.removeEventListener('message', eventListener)
      console.log("REMOVE EVENT LISTENER")
      return
    }
      
    console.log(("ADD EVENT LISTENER"))

    const e = window.addEventListener('message', message => {
      let data = JSON.parse(message.data)

      console.log(message.data)

      if(data.event == 'infoDelivery' && data.info && data.info.currentTime) {
          console.log(data.info.currentTime, " ", currentTime)
        setCurrentTime(data.info.currentTime)
        onTick(data.info.currentTime)
      }
    });

    setEventListener(e)

    return () => {
      if(eventListener)
        window.removeEventListener('message', eventListener)
    };
  }, [playing]);



  useEffect(() => {
    if(seekTo) {
      player.seekTo(seekTo)
      if(!playing)
        player.playVideo()
    }
  }, [seekTo, player]);

  useEffect(() => {
     if(player)
       player.setSize(width, height)
  }, [width, height]);


  useEffect(() => {
    if(scrollTrigger)
      setBoxClassName(videoCSS.base + " " + videoCSS.scrollTrigger)
    else
      setBoxClassName(videoCSS.base)
  }, [scrollTrigger]);

  return (
    <>
      <Paper elevation={ playing ? 13 : 3 } className={boxClassName}>
        <YouTube
          containerClassName={videoCSS.iframeContainer} 
          videoId={videoId} 
          onReady={onYoutubeReady}
          onPlay={onPlay}
          onPause={onStop}
          onEnd={onStop}
        />
      </Paper>
    </>       
  )
}