import React, { useState } from "react"
import Container from '@material-ui/core/Container';
import Video from './Video'
import Transcription from './Transcription'
import styles from './TimeManager.module.css'

export default function TimeManager({youtube, body}){
  const [time, setTime] = useState(0);
  const [seekTo, setSeekTo] = useState(null);
  const [hoverOn, setHoverOn] = useState(null);
  const [timeoutId, setTimeoutId] = useState(null);

  function onTick(newTime /*, duration */) {
    setSeekTo(null)
    setTime(newTime)
  }

  function handleClick(newTime/* , duration */) {
    setSeekTo(newTime);
  }

  return (
    <div className={styles.timemanager}>

      <div className={styles.videoindex}>

        <div className={styles.video}>
          <Video videoId={youtube} visible={true} width={360} height={180} onTick={onTick} seekTo={seekTo} hoverOn={hoverOn} />
        </div>

      </div>
      
      <Container maxWidth="md" className={styles.transcription}>
        <Transcription
          time={time}
          body={body}
          onClick={handleClick}
        />
      </Container>

    </div>
    )
}