import React, { useState, useEffect } from "react"
import Subtitle from "./Subtitle"

import paragraphCSS from "./Paragraph.module.css"

export default function Paragraph(props) {
  const { start, reach, current, lines, autoScroll, onClick } = props
  const [className, setClassName] = useState(paragraphCSS.base);

  useEffect(() => {
    setClassName(`
      ${paragraphCSS.base}
      ${reach ? paragraphCSS.reach : ''}
      ${current ? paragraphCSS.current : ''}
    `)
  }, [reach, current]);

  return(
    <p className={className}>
      {lines.map((line, index) =>
        <Subtitle key={index} start={line.start} reach={line.reach} current={line.current} autoScroll={autoScroll} onClick={onClick} >
          {line.text}
        </Subtitle>
      )}
    </p>
  )
}